//Angular
import { Injectable, Inject, EventEmitter } from '@angular/core';

import { MdDialog } from '@angular/material';
import { DialogComponent } from '../components/dialog/dialog.component';

import { Observable } from 'rxjs';
export interface Place {
  address_components: any[];
  adr_address: string;
  formatted_address: string;
  geometry: any;
  icon: string;
  id: string;
  name: string;
  place_id: string;
  reference: string;
  scope: string;

  // and more
}

interface Edge {
  node: Node;
  distance: number;
}

export interface Node {
  place: Place;
  neighbours: Edge[];
}



@Injectable()
export class MapsService {

  private _window: any;

  private directionsService;
  private directionsDisplay;
  private map;
  private markers = [];
  private places: Place[] = [];
  private directionsRenderer;
  private markersRoute = [];
          //directions.setMap(this.map);

          private counter = 1;

          private directionsDisplayArray: Array<any> = [];

  private placesEmitter: EventEmitter<Place[]> = new EventEmitter<Place[]>();

  constructor(@Inject('Window') window: Window, public dialog: MdDialog) {
    this._window = window;
  }

  getPlaces(): Place[] {
    return this.places;
  }

  onPlacesChange(): Observable<Place[]> {
    return this.placesEmitter.asObservable();
  }

  setupMapService(map: any) {
    this.directionsService = new this._window.google.maps.DirectionsService;
    this.directionsDisplay = new this._window.google.maps.DirectionsRenderer({suppressMarkers: true});
    this.map = map;
    this.directionsDisplay.setMap(this.map);
  }

  loadMap() {
    this.directionsService = new this._window.google.maps.DirectionsService;
    this.directionsDisplay = new this._window.google.maps.DirectionsRenderer({suppressMarkers: true});
    this.map = new this._window.google.maps.Map(document.getElementById('map'), {
      zoom: 7,
      center: {lat: 41.85, lng: -87.65},
      mapTypeId: this._window.google.maps.MapTypeId.ROADMAP
    });
    this.directionsDisplay.setMap(this.map);
  }

  loadSearchBox() {
    this.markers = [];
    // Create the search box and link it to the UI element.
    var input = document.getElementById('pac-input');
    var searchBox = new this._window.google.maps.places.SearchBox(input);
    //this.map.controls[this._window.google.maps.ControlPosition.TOP_LEFT].push(input);

    // Bias the SearchBox results towards current map's viewport.
    this.map.addListener('bounds_changed', () => {
      searchBox.setBounds(this.map.getBounds());
    });

    // [START region_getplaces]
    // Listen for the event fired when the user selects a prediction and retrieve
    // more details for that place.
    searchBox.addListener('places_changed', () => {
      var places = searchBox.getPlaces();
      if (places.length == 0) {
        return;
      }

      // Clear out the old markers.
      this.markers.forEach(function(marker) {
        marker.setMap(null);
      });


      // For each place, get the icon, name and location.
      var bounds = new this._window.google.maps.LatLngBounds();
      places.forEach((place) => {
        console.log('place', place);
        // save places
        this.places.push(place);
        // emit new place to subscribers
        this.placesEmitter.emit(this.places);
        var icon = {
          url: place.icon,
          size: new this._window.google.maps.Size(71, 71),
          origin: new this._window.google.maps.Point(0, 0),
          anchor: new this._window.google.maps.Point(17, 34),
          scaledSize: new this._window.google.maps.Size(25, 25)
        };

        // Create a marker for each place.
        this.markers.push(new this._window.google.maps.Marker({
          map: this.map,
          icon: icon,
          title: place.name,
          position: place.geometry.location
        }));

        if (place.geometry.viewport) {
          // Only geocodes have viewport.
          bounds.union(place.geometry.viewport);
        } else {
          bounds.extend(place.geometry.location);
        }
      });
      this.map.fitBounds(bounds);
    });
  }

  showRoute(origin: string, destination: string) {
    this.directionsService.route({
      origin: origin,
      destination: destination,
      travelMode: this._window.google.maps.TravelMode.DRIVING
    }, (response, status) => {
        console.log("response", response);
        if (status === this._window.google.maps.DirectionsStatus.OK) {
          let directions = new this._window.google.maps.DirectionsRenderer({suppressMarkers: true});
          directions.setMap(this.map);
          directions.setDirections(response);
          this.directionsDisplayArray.push(directions);
        } else {
          window.alert('Directions request failed due to ' + status);
        }
      });
  }

  cleanMarkers() {
      // Clear out the old markers.
      this.markers.forEach(function(marker) {
        marker.setMap(null);
      });
      this.markersRoute.forEach(function(marker) {
        marker.setMap(null);
      });
  }

  cleanMap() {
    this.counter = 1;
    this.directionsDisplayArray.forEach(directions => directions.setMap(null) );
    this.cleanMarkers();
  }

  calcPath(firstPlace: Place, allPlaces: Place[]): Place[] {
    let places: Place[] = [];
    // add first place
    places.push(firstPlace);
    // filter places
    let filterPlaces = allPlaces.filter((p)=> p.id !== firstPlace.id);
    console.log('filter places', filterPlaces);

    let observables: any[] = [];

    filterPlaces.forEach((place) => {
      observables.push(this.getDistance(firstPlace, place ));
    });

    if(filterPlaces.length === 0) {
      // show last route
      setTimeout(()=>{this.showRoute(firstPlace.formatted_address, this.places[0].formatted_address);}, 2000);
      return;
    }

    Observable.zip(...observables)
              .delay(2000)
              .do(console.log)
              .map((distances: PlaceDistance[]) => distances[minDistance(distances.map(x => x.distance))])
              .subscribe(
                (placeDistance: PlaceDistance) => {
                  console.log('min', placeDistance);
                  // save origin as a visited
                  // draw
                  let directions = new this._window.google.maps.DirectionsRenderer({suppressMarkers: true});
                  directions.setMap(this.map);
                  directions.setDirections(placeDistance.responseMaps);
                  this.directionsDisplayArray.push(directions);

                  let leg = placeDistance.responseMaps.routes[ 0 ].legs[ 0 ];
                  this.makeMarker( leg.start_location, this.counter+'' );
                  this.makeMarker( leg.end_location, (this.counter+1)+'' );
                  this.counter++;
                  // calculate next
                  if(filterPlaces.length > 0) {
                    this.calcPath(placeDistance.destination, filterPlaces);
                  }
                },
                (error) => {
                  //this.dialog.open(DialogComponent);
                  alert('Google maps error ocurred');
                });


    return places;

  }

  private makeMarker( position, label ): any {
    this.markersRoute.push(new this._window.google.maps.Marker({
      position: position,
      map: this.map,
      label: label
    }));
  }


  getDistance(origin: Place, destination: Place): Observable<PlaceDistance> {
    return new Observable(
      (observer) => {
         this.directionsService.route({
            origin: origin.formatted_address,
            destination: destination.formatted_address,
            travelMode: this._window.google.maps.TravelMode.DRIVING
          }, (response, status) => {
              console.log("response", response);
              console.log("status", status);

              if(!response || status === 'OVER_QUERY_LIMIT') {
                observer.error();
              }

              console.log("origin, dest", [origin, destination]);
              if (status === this._window.google.maps.DirectionsStatus.OK && response.routes[0].legs[0].distance) {
                // console.log(response);
                observer.next({
                  origin: origin,
                  destination: destination,
                  distance: response.routes[0].legs[0].distance.value,
                  responseMaps: response
                });
                observer.complete();
              } else {
                observer.error();
              }
            });
        } );
    }
  }

// Utils
const minDistance = (distances: number[]) => distances.indexOf(Math.min(...distances));

interface PlaceDistance {
  origin: Place;
  destination: Place;
  distance: number;
  responseMaps: any;
}



