import { Component, ViewChild, NgZone } from '@angular/core';

import { MapsService, Place } from './maps.service';

import { MdDialog } from '@angular/material';
import { DialogComponent } from '../components/dialog/dialog.component';

import { LazyMapsAPILoader, LAZY_MAPS_API_CONFIG, LazyMapsAPILoaderConfigLiteral } from '@agm/core';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {

  @ViewChild('map') mapRef: any;

  title: string = 'Fastest Route';
  lat: number = 41.3994083;
  lng: number = 2.160641;

  latA: number = 41.4021253;
  lngA: number = 2.1759248;

  latB: number = 41.3913891;
  lngB: number = 2.1695639;

  placesNames: string[] = [];

  constructor(
    private mapsService: MapsService,
    private lazyMapsAPILoader: LazyMapsAPILoader,
    private ngZone: NgZone,
    private dialog: MdDialog
  ) {


    this.mapsService.onPlacesChange().subscribe(
      (places: Place[]) => {
        console.log('on places change', places);
        this.ngZone.run(
          () => {
            this.placesNames = places.map(x=>x.name);
          }
        );
      }
    );
  }

  ngAfterViewInit() {
    console.log('map', this.mapRef);
  }

  doSearch() {

  }

  onMapReady(map) {
    console.log('map ready!!', map);
    this.mapsService.setupMapService(map);
    this.mapsService.loadSearchBox();
  }

  addFavourite() {

  }

  loadMap() {

    this.mapsService.loadMap();
  }

  loadSearch() {
    /*this.mapsService.showRoute('chicago, il', 'joplin, mo');

    this.mapsService.showRoute('joplin, mo', 'oklahoma city, ok');*/
  }

  calcRoute() {
    let places: Place[] = this.mapsService.getPlaces();
    if(this.placesNames.length > 7) {
      this.dialog.open(DialogComponent);
      return;
    }
    this.placesNames = places.map(x=>x.name);
    console.log('calcRoute', places);
   /* for(let i = 0; i < places.length-1; i++) {
      this.mapsService.showRoute(places[i].formatted_address, places[i+1].formatted_address);
    }*/

    this.mapsService.cleanMap();
    this.mapsService.calcPath(places[0], places);
  }


  loadSearchBox() {
    this.mapsService.loadSearchBox();
  }

  removePlace(index: number) {
    this.placesNames.splice(index, 1);
    this.mapsService.getPlaces().splice(index, 1);
  }

}






