import { Component, OnInit, EventEmitter, Input, Output, ChangeDetectionStrategy } from '@angular/core';

export interface SoupListItem {
  name: string;
}

@Component({
  selector: 'soup-list',
  styleUrls: [ './soup-list.component.css' ],
  templateUrl: './soup-list.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class SoupListComponent implements OnInit {

  /**
   * Title of favourites component default set to 'Favourites'
   */
  @Input() title: string = '';

  /**
   * Input recieving the list of favourites to represent as an Immutable data because we work
   * with change detection strategy OnPush
   */
  @Input() listItems: string[] = ['AAA', 'BBBB', 'CCCC'];

  @Output() itemRemoved: EventEmitter<number> = new EventEmitter<number>();

  constructor() { }

  public ngOnInit() {

  }

  remove(index: number) {
    console.log('remove');
    //this.listItems.splice(index, 1);
    this.itemRemoved.next(index);
  }


}



