import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';

import { AppComponent } from './app.component';
import { MapsService } from './maps.service';

// import components
import { APP_COMPONENTS } from '../components'
import { DialogComponent } from '../components/dialog/dialog.component';

import { AgmCoreModule, LazyMapsAPILoader, LAZY_MAPS_API_CONFIG, LazyMapsAPILoaderConfigLiteral } from '@agm/core';
import { MaterialModule } from '@angular/material';

const MapsConfig: LazyMapsAPILoaderConfigLiteral = {
  apiKey: 'AIzaSyBGwsw_9Idnz8UnqKLoFqEb2XRUh9apzTQ',
  apiVersion: undefined,
  channel:'',
  libraries: ['places']
}

@NgModule({
  declarations: [
    AppComponent,
    DialogComponent,
    ...APP_COMPONENTS
  ],
  entryComponents: [
    DialogComponent
  ],
  imports: [
    BrowserModule,
    FormsModule,
    HttpModule,
    MaterialModule,
    AgmCoreModule.forRoot({
      apiKey: 'AIzaSyBGwsw_9Idnz8UnqKLoFqEb2XRUh9apzTQ',
      libraries: ['places']
    })
  ],
  providers: [
    MapsService,
    LazyMapsAPILoader,
    //{provide: LAZY_MAPS_API_CONFIG, useValue: MapsConfig},
    { provide: 'Window',  useValue: window }
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }






